* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-flag {
  width: 90px;
  margin-right: 10px;
}

.country-info,
.country-list {
  margin-left: 40px;
}

.country-name {
  font-weight: 500;
  font-size: 20px;
}

.description-item {
  font-size: 20px;
}

.description-item span {
  font-weight: 700;
}
